import React, { Component } from 'react';
import * as sg from './Sudokugenerator.js';
  export default class Boardr extends React.Component {
    constructor(props){
      super(props);
      let startingBoard = sg.generateStartingBoard(this.props.initial);
      this.state = {
        squares: startingBoard,
        status: {
          msg: '',
          color: 'blue'
        }
       
      }

    };

    handleKeyPress = (e) => {
      if (e.key === "enter"){
        e.target.blur();
      }
    }

    handleSubmit(e,i) {
      const squares =this.state.squares.slice();
      squares[i].value = e.target.value;
      this.setState({squares: squares});

    }
    
    createBoard()
    {
      let board= [];
      let row;
      let block;
      for(let i = 0; i < 9; i++){
        block = [];
        for(let j = 0; j <3; j++){
          row = [];
          for(let k = 0; k <3; k++){
            row.push(this.renderSquare(i*9+j*3+k));
          }
          block.push(<div className="board-row" key={j}>{row}</div>);
        }
        board.push(<div className="block" key={i}>{block}</div>)
      }
      return(board);
     }
     renderSquare(i){
      //let disabled = false;
      //if(this.state.squares[i].initial)
      //return(i)
          console.log(this.state.squares[i])
      let disabled = false;
      if (this.state.squares[i].initial) {
        disabled = true;
      }
  
      let className = "";
      this.state.squares[i].classes.forEach((element) => className += element);
  
      return (
        <Square 
          value={this.state.squares[i].value} 
          disabled={disabled} 
          className={className} 
          key={i} 
          onKeyDown={this.handleKeyPress} 
          onChange={(e) => this.handleSubmit(e, i)}
        />);
      }
       
    render(){
      return (
      <div>
        <h2 className="title">Sudoku</h2>
        {this.createBoard()}
      </div>
      );
    }
  }

  class Square extends React.Component{
    render(){
      return(
        <input
        value={this.props.value}
        type="text"
        disabled={this.props.disabled} 
        pattern="[1-9]"
        maxLength="1"
        className={this.props.className}
        onChange={this.props.onChange}
        onKeyDown={this.props.onKeyDown}/>

      )
    }

  }
