import React, { Component } from 'react';
import {FormGroup, ControlLabel, FormControl, Button} from 'react-bootstrap';

class GameMenu extends React.Component {
    constructor(props) {
        super(props);
       
        this.state = {
                value: 33
        };
        this.changefunction= this.changefunction.bind(this);
    }
    changefunction(events){
        //console.log(events)
        this.setState({value: events.target.value});
        console.log(events.target.value)
    };
    renderOption(start,end) {
        let i;
        let result =[];
        for(i=start;i<end; i++){
            result.push(<option key= {'option-' +i}  value={(i)}>{i}</option>);
        }
        return result;
    }

    render(){
        const { onGeneratess }= this.props;
        return(
        <div>    
                  
            <h3>Generate </h3>
            <h4>Initial cell </h4>
            <form>
            <select className="change" onChange={this.changefunction} value={this.state.value}> 
            <option value="17-extreme">17-extreme</option>
             {this.renderOption(18,26)}
            <option value="26-hard">26-hard</option>
            {this.renderOption(27,33)}
            <option value="33-medium">33-medium</option>
            {this.renderOption(34,40)}
            <option value="40-easy">40-easy</option>
            {this.renderOption(41,50)}
            <option value="50-beginner">50-beginner</option>
            {this.renderOption(51,65)}
            <option value="65-baby">65-baby</option>
            {this.renderOption(65,81)}
            <option value="81">81-solved</option>
            </select>
            </form>
          
           
        <Button
          className="generate-btn"
          onClick={() => {
            this.props.onGenerate(this.state.value);
          }}
        >
          Generate
        </Button>
        <p className="instruction"> Select the number of initial cells, then click generate!</p>
        </div>
        );
    }
}
export default GameMenu;