import React, { Component } from 'react';
import GameMenu from './Game';
import 'bootstrap/dist/css/bootstrap.css';
import Boardr from './Board';

class GameIndex extends React.Component {
    constructor(props) {
      super(props);
        this.state = {
            initial: 33
        };
    }

    handleGeneration = (initialss) => {
        this.setState({initial: initialss});
    }

    render(){
          return(
            <div className="sudoku">
            <div className="game">
             <div className="game-board"> 
               <Boardr initial= {this.state.initial}/>
               
                <div className="game-menu">
                 <GameMenu onGenerate={this.handleGeneration}/>
                 
                </div>
                
             </div>
                
                
            </div>  
            </div>

                 
        ); 
    }
}
export default GameIndex;