import React, { Component } from 'react';
import update from 'immutability-helper';
import math from'mathjs';
import Buttons from './Buttons';
import Button from './Button';
import Displays from './Display';
class Calculators extends React.Component {
    constructor(){
        super()
        this.state={operation:[]}
    }
    calculateoperation = () =>{
      let  result=this.state.operation.join('')
      if(result){
        result=math.eval(result)
        result=math.format(result, {precision: 14})
        this.setState({
            operation:[result]
      
     
      })
     }

    }
      
    handleClick = e => {
        const value = e.target.getAttribute('data-value')
        switch(value){
            case 'clear' :
            this.setstate({
                operation :[]

            })
            break
            case 'equal' :
            this.calculateoperation()
            break
            default:
            const newOperation = update(this.state.operation,{
                $push: [value]
            })
            this.setState({
               operation: newOperation, 
            })
break
            
            

            
        }
       
    }
    render(){
        return(
        <div className="Calculatr">
            <h4>calculator </h4>
            <Displays datay={this.state.operation}/>

            <Buttons>
                <Button onClick={this.handleClick} label="C" value="Clear"/>
                <Button onClick={this.handleClick} label="7" value="7" />
                <Button onClick={this.handleClick} label="4" value="4" />
                <Button onClick={this.handleClick} label="1" value="1" />
                <Button onClick={this.handleClick} label="0" value="0" />

                <Button onClick={this.handleClick} label="/" value="/" />
                <Button onClick={this.handleClick} label="8" value="8" />
                <Button onClick={this.handleClick} label="5" value="5" />
                <Button onClick={this.handleClick} label="2" value="2" />
                <Button onClick={this.handleClick} label="." value="." />

                <Button onClick={this.handleClick} label="x" value="*" />
                <Button onClick={this.handleClick} label="9" value="9" />
                <Button onClick={this.handleClick} label="6" value="6" />
                <Button onClick={this.handleClick} label="3" value="3" />
                <Button onClick={this.handleClick} label="" value="null" />

                <Button onClick={this.handleClick} label="-" value="-" />
                <Button onClick={this.handleClick} label="+" size="2" value="+" />
                <Button onClick={this.handleClick} label="=" size="2" value="equal" />
                </Buttons>
                
                </div>
        
          
            
        )
    }
}

export default Calculators;